@import url('https://fonts.googleapis.com/css?family=Patua+One|Lato:300');

html {
 -webkit-box-sizing: border-box;
         box-sizing: border-box;
}

*, *:before, *:after {
 -webkit-box-sizing: inherit;
         box-sizing: inherit;
}

body {
  margin: 0;
  font-family: 'Lato', sans-serif;
  padding: 0;
  background-color: #f1f2f3;
  line-height: 1.7;
}

@media (min-width: 992px) {
  body {
    font-size: 18px;
  }
}

img {
  max-width: 100%;
  display: block;
  margin: auto;
}

p {
  margin: 0.5em;
  font-size: 1.1em;
}

a {
  text-decoration: none;
  color: inherit;
}

a:hover {
  color: inherit;
}

h1, h2, h3 {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

/***************************************
  Styles for ReactCSSTransitionGroup
****************************************/

.title-appear {
  opacity: 0.01;
  visibility: hidden;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}

.title-appear.title-appear-active {
  opacity: 1;
  visibility: visible;
  -webkit-transform: translateX(0%);
      -ms-transform: translateX(0%);
          transform: translateX(0%);
  -webkit-transition: all 1.5s ease;
  -o-transition: all 1.5s ease;
  transition: all 1.5s ease;
}

.intro-appear {
  opacity: 0.01;
  visibility: hidden;
  -webkit-transform: translateX(50%);
      -ms-transform: translateX(50%);
          transform: translateX(50%);
}

.intro-appear.intro-appear-active {
  opacity: 1;
  visibility: visible;
  -webkit-transform: translateX(0%);
      -ms-transform: translateX(0%);
          transform: translateX(0%);
  -webkit-transition: all 1.5s ease;
  -o-transition: all 1.5s ease;
  transition: all 1.5s ease;
}

.fade-in-appear {
  opacity: 0.01;
  visibility: hidden;
}

.fade-in-appear.fade-in-appear-active {
  opacity: 1;
  visibility: visible;
  -webkit-transition: all 0.5s ease-in;
  -o-transition: all 0.5s ease-in;
  transition: all 0.5s ease-in;
}

/***************************************
  Styles for WorkItem tiles
****************************************/

.work-item-tile {
  position: relative;
  overflow: hidden;
  min-height: 15em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.work-item-img {
  max-width: 170px;
  -webkit-transition: -webkit-transform 0.4s;
  transition: -webkit-transform 0.4s;
  -o-transition: transform 0.4s;
  transition: transform 0.4s;
  transition: transform 0.4s, -webkit-transform 0.4s;
}

.work-item-tile:hover .work-item-img,
.work-item-tile:focus .work-item-img,
.work-item-tile:active .work-item-img {
  -webkit-transform: translateY(-50px);
      -ms-transform: translateY(-50px);
          transform: translateY(-50px);
}

.work-item-tile:hover .work-item-overlay,
.work-item-tile:focus .work-item-overlay,
.work-item-tile:active .work-item-overlay {
  opacity: 1;
  -webkit-transform: translateY(0px);
      -ms-transform: translateY(0px);
          transform: translateY(0px);
  -webkit-transition: opacity 0.1s, -webkit-transform 0.4s;
  transition: opacity 0.1s, -webkit-transform 0.4s;
  -o-transition: transform 0.4s, opacity 0.1s;
  transition: transform 0.4s, opacity 0.1s;
  transition: transform 0.4s, opacity 0.1s, -webkit-transform 0.4s;
}

.work-item-overlay {
  padding: 0 1em;
  background: #2c3f52;
  color: #fffbfe;
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: 100px;
  width: 100%;
  top: auto;
  bottom: 0;
  opacity: 0;
  -webkit-transform: translateY(100%);
      -ms-transform: translateY(100%);
          transform: translateY(100%);
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transition: opacity 0.1s 0.3s, -webkit-transform 0.4s;
  transition: opacity 0.1s 0.3s, -webkit-transform 0.4s;
  -o-transition: transform 0.4s, opacity 0.1s 0.3s;
  transition: transform 0.4s, opacity 0.1s 0.3s;
  transition: transform 0.4s, opacity 0.1s 0.3s, -webkit-transform 0.4s;
  text-align: center;
}

.work-item-overlay:hover {
  opacity: 1;
  -webkit-transform: translateY(0px);
      -ms-transform: translateY(0px);
          transform: translateY(0px);
  -webkit-transition: opacity 0.1s, -webkit-transform 0.4s;
  transition: opacity 0.1s, -webkit-transform 0.4s;
  -o-transition: transform 0.4s, opacity 0.1s;
  transition: transform 0.4s, opacity 0.1s;
  transition: transform 0.4s, opacity 0.1s, -webkit-transform 0.4s;
}



